import React from 'react';
import ReactEcharts from 'echarts-for-react';
import classNames from 'classnames';
import styles from './index.less';

type Props = {
  data: any;
};
type State = {
  data: any[];
  pageIndex: number;
};
let myChart: any = {};
let index = 0;
class PieChart extends React.Component<Props, State> {
  loopTooltipTimer: any;
  echartsReact: any;
  // index: 0;
  // myChart: any;
  timer: any;
  constructor(props) {
    super(props);
    this.echartsReact = React.createRef();
  }
  state = {
    pageIndex: 0,
    data: [],
  };
  componentDidMount() {
    const data = this.props.data.data;
    this.setState({ data: data.filter((_, index) => index < 7) });
    myChart = this.echartsReact.getEchartsInstance();
    this.autoTooltip();
  }
  componentWillUnmount() {
    clearTimeout(this.timer);
    myChart && myChart.clear();
  }

  // tooltip轮播
  autoTooltip = () => {
    const dataLength = this.state.data.length;
    const _this = this;
    this.timer && clearTimeout(this.timer);
    this.timer = setTimeout(function() {
      myChart.dispatchAction({
        type: 'showTip',
        seriesIndex: 0,
        dataIndex: index,
      });
      index++;
      if (index >= dataLength) {
        index = 0;
      }
      _this.autoTooltip();
    }, 2000);

    myChart.on('mouseout', params => {
      index = params.dataIndex;
    });
  };
  render() {
    const { data }: any = this.state;

    const yAxisData = (data || []).map(item => item.area) || [];
    const securityStaffData = (data || []).map(item => item.securityStaff) || [];
    const closedData = (data || []).map(item => item.closed) || [];
    const alarmData = (data || []).map(item => item.alarm) || [];
    const videoData = (data || []).map(item => item.video) || [];
    const protectData = (data || []).map(item => item.protect) || [];

    // const staffTotal = sum(staffData);
    // const securityGuardsTotal = sum(securityGuardsData);
    // const studentsTotal = sum(studentsData);

    // const labelOption = total => ({
    //   show: true,
    //   position: 'inside',
    //   color: '#fff',
    //   fontSize: 12,
    //   formatter: function(data) {
    //     return data.value;
    //   },
    // });

    const option = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'none',
        },
        backgroundColor: 'rgba(15, 103, 171, 0.45)',
        borderWidth: 0,
        className: styles.tooltip,
        formatter: function(params) {
          var relVal = params[0].name;
          for (var i = 0, l = params.length; i < l; i++) {
            relVal +=
              '<br/>' +
              params[i].marker +
              params[i].seriesName +
              '<span style="float:right;padding-left:15px">' +
              params[i].value +
              '%</span>';
          }
          return relVal;
        },
        textStyle: {
          color: '#fff',
          fontSize: 15,
        },
      },
      legend: {
        show: true,
        top: 12,
        itemWidth: 16,
        itemHeight: 10,
        itemGap: 15,
        icon: 'rect',
        textStyle: {
          padding: 4,
          color: '#fff',
          fontSize: 14,
          fontFamily: 'PingFangSC-Regular, PingFang SC',
          fontWeight: 400,
        },
      },
      grid: {
        left: '2%',
        right: '3%',
        bottom: '6%',
        top: '25%',
        containLabel: true,
      },
      yAxis: [
        {
          type: 'category',
          data: yAxisData,
          inverse: true,
          axisLabel: {
            color: '#fff',
            fontWeight: 300,
            padding: 0,
          },
          axisTick: {
            show: false,
          },
        },
      ],
      xAxis: [
        {
          type: 'value',
          axisLabel: { show: false },
          splitLine: {
            show: false,
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.4)',
            },
          },
        },
      ],
      series: [
        {
          name: '校园专职保安员配备',
          type: 'bar',
          stack: 'Ad',
          barWidth: 20,
          color: 'rgba(4, 209, 255, 1)',
          data: securityStaffData,
        },
        {
          name: '封闭化管理',
          type: 'bar',
          barWidth: 20,
          stack: 'Ad',
          color: 'rgba(85, 139, 254, 1)',
          data: closedData,
        },
        {
          name: '一键式紧急报警',
          type: 'bar',
          barWidth: 20,
          color: 'rgba(37, 92, 209, 1)',
          stack: 'Ad',
          data: alarmData,
        },
        {
          name: '视频监控系统联网',
          type: 'bar',
          barWidth: 20,
          color: 'rgba(26, 64, 141, 1)',
          stack: 'Ad',
          data: videoData,
        },
        {
          name: '护学岗建设',
          type: 'bar',
          barWidth: 20,
          color: 'rgba(80, 89, 178, 1)',
          stack: 'Ad',
          data: protectData,
        },
      ],
    };

    return (
      <div
        className={classNames(styles.container)}
        onMouseOver={() => {
          clearTimeout(this.timer);
        }}
        onMouseOut={() => {
          clearTimeout(this.timer);
          this.autoTooltip();
        }}
      >
        <ReactEcharts
          ref={e => {
            this.echartsReact = e;
          }}
          option={option}
          notMerge={true}
          lazyUpdate={true}
          style={{
            width: '100%',
            height: '100%',
            backgroundColor: 'transparent',
            margin: '0',
            zIndex: 6,
          }}
        />
        <div className={styles.pageWrap}>
          <div className={styles.content}>
            {[...new Array(2).keys()].map((_, index) => {
              return (
                <div
                  key={index.toString(36) + index}
                  onClick={() => {
                    const data = this.props.data.data;

                    this.setState(
                      { pageIndex: index, data: data.slice(7 * index, 7 * index + 7) },
                      () => {
                        index = 0;
                      },
                    );
                  }}
                  className={classNames(
                    styles.page,
                    index === this.state.pageIndex && styles.selected,
                  )}
                />
              );
            })}
          </div>
        </div>
      </div>
    );
  }
}

export default PieChart;
