const countyData = globalCountyData.map(item => ({ ...item, x: item.x - 960, y: item.y - 540 }));

const County: FC<{
  options: {
    onMouseEnter?: MouseEventHandler<any> | undefined;
    onMouseLeave?: MouseEventHandler<any> | undefined;
  };
  left: number;
  top: number;
  name: string;
  selected?: boolean;
  nameOffset?: number;
  item: any;
}> = ({ name, selected, left, top, options, nameOffset, item }) => {
  return (
    <div className={classNames(styles.county)} style={{ left, top }} {...options}>
      <>
        {!selected ? (
          <div>
            <div className={classNames(styles.circleGroup)}>
              <div className={styles.smallCircle} />
              <div className={styles.circle} />
            </div>
            <div className={styles.rect} />
          </div>
        ) : (
          <div className={classNames(styles.selectedCityBg)}>
            <img src={selectedCityBg} />
          </div>
        )}

        <div className={styles.name} style={{ marginLeft: nameOffset + 'px' }}>
          {name}
        </div>
      </>
    </div>
  );
};

const CountyInfo = memo(
  ({
    name = '',
    x,
    y,
    composite = 0,
    schoolTotal = 0,
    districtId,
    zhiAnCampusesTotal,
  }: {
    composite: number;
    schoolTotal: number;
    zhiAnCampusesTotal: number;
    districtId: number;
    nurserySchool: number;
    primarySchool: number;
    juniorHighSchool: number;
    highSchool: number;
    university: number;
    x: number;
    y: number;
    name?: string;
  }) => {
    if (!name) {
      return null;
    }
    return (
      <div className={styles.mapInfo} style={{ transform: `translate(${x}px, ${y}px)` }}>
        <div className={classNames(styles.mapInfoLine, districtId === 7 && styles.overturn)}>
          <img src={mapInfoLine} />
        </div>
        <div className={styles.title}>{name || ''}</div>
        <div className={styles.info}>
          <span>智安综合指数 :</span>
          <span style={{ paddingLeft: '6px', color: '#ff7e16' }}>{composite}</span>
        </div>
        <div className={styles.info}>
          <span>学校总数 :</span>
          <span style={{ paddingLeft: '6px', color: '#ff7e16' }}>{schoolTotal}</span>
        </div>
        <div className={classNames(styles.info)}>
          <span>智安校园总数 :</span>
          <span style={{ paddingLeft: '6px', color: '#ff7e16' }}>{zhiAnCampusesTotal}</span>
        </div>
      </div>
    );
  },
);

const Map = ({ data }: { data: any }) => {
  const [countyInfo, setCountyInfo] = useState<any>({});
  const timeRef = useRef<any>();
  let autoTimerRef = useRef<any>();
  let index = 0;
  const mapInfoWidth = 210;
  const mapInfoPaddingLeft = 94;
  const offset = mapInfoWidth + mapInfoPaddingLeft + mapInfoPaddingLeft;
  const autoTooltip = () => {
    if (data && Array.isArray(countyData)) {
      const dataLength = countyData.length;
      autoTimerRef.current = setTimeout(function() {
        const item = countyData[index];
        const current = ((data && data.districtInfos) || {})[item.districtId] || {};
        index++;
        if (index >= dataLength) {
          index = 0;
        }
        setCountyInfo({
          ...current,
          ...item,
          x: item.districtId === 7 ? item.x - offset : item.x,
        });
        autoTooltip();
      }, 2000);
    }
  };
  useEffect(() => {
    autoTooltip();
    return () => {
      clearTimeout(autoTimerRef.current);
    };
  }, []);

  return (
    <div className={styles.mapWrap}>
      <div className={styles.city}>
        {countyData.map((item, i) => {
          return (
            <County
              options={{
                onMouseEnter: e => {
                  clearTimeout(timeRef.current);
                  clearTimeout(autoTimerRef.current);
                  const current = ((data && data.districtInfos) || {})[item.districtId];
                  setCountyInfo({
                    ...current,
                    ...item,
                    x: item.districtId === 7 ? item.x - offset : item.x,
                  });
                },
                onMouseLeave: e => {
                  timeRef.current = setTimeout(() => {
                    index = i;
                    setCountyInfo({});
                    autoTooltip();
                  }, 500);
                },
              }}
              nameOffset={item.districtId === 5 ? -24 : 0}
              selected={item.districtId === countyInfo.districtId}
              left={item.x - 8}
              top={item.y + 5}
              name={item.name}
              item={item}
            />
          );
        })}
      </div>
      {<CountyInfo {...countyInfo} />}
    </div>
  );
};

const MapComponent = ({ data }) => {
  return <Map data={data} />;
};

export default MapComponent;
