import React, { memo } from 'react';
import styles from './index.less';
import FlipClock from './components/FlipClock';

interface Props {
  data: number[];
  title: string;
}
const DigitalFlipper = memo(({ data = [], title }: Props) => {
  return (
    <div className={styles.digitalFlipperWrap}>
      <div className={styles.title}>{title}</div>
      <div>
        {data && data.length === 5 && (
          <>
            <FlipClock gotoNum={data[0]} delay={100} />
            <FlipClock gotoNum={data[1]} delay={30} />
            <FlipClock gotoNum={data[2]} delay={200} />
            <FlipClock gotoNum={data[3]} delay={300} />
            <FlipClock gotoNum={data[4]} delay={400} />
          </>
        )}
      </div>
    </div>
  );
});
export default DigitalFlipper;
