import React from 'react';
import ReactEcharts from 'echarts-for-react';
import classNames from 'classnames';
import styles from './index.less';
import { xAxisLineFeed } from '../../util';

type Props = {
  data: any;
};
let myChart: any = {};
let index = 0;
class PieChart extends React.Component<Props, {}> {
  loopTooltipTimer: any;
  echartsReact: any;
  // index: 0;
  // myChart: any;
  timer: any;
  constructor(props) {
    super(props);
    this.echartsReact = React.createRef();
  }
  componentDidMount() {
    myChart = this.echartsReact.getEchartsInstance();
    this.autoTooltip();
  }
  componentWillUnmount() {
    clearTimeout(this.timer);
    myChart && myChart.clear();
  }

  autoTooltip = () => {
    if (!this.props.data.data) return null;
    const dataLength = this.props.data.data.length;
    const _this = this;
    this.timer && clearTimeout(this.timer);
    this.timer = setTimeout(function() {
      myChart.dispatchAction({
        type: 'showTip',
        seriesIndex: 0,
        dataIndex: index,
      });
      index++;
      if (index >= dataLength) {
        index = 0;
      }
      _this.autoTooltip();
    }, 2000);

    myChart.on('mouseout', params => {
      index = params.dataIndex;
    });
  };
  render() {
    const { data } = this.props;
    const xAxisData = (data.data || []).map(item => item.area) || [];
    const normalData = (data.data || []).map(item => item.normal) || [];
    const overageData = (data.data || []).map(item => item.overage) || [];
    const withoutLicenseData = (data.data || []).map(item => item.withoutLicense) || [];

    const option = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'none',
        },
        backgroundColor: 'rgba(15, 103, 171, 0.45)',
        borderWidth: 0,
        className: styles.tooltip,
        textStyle: {
          color: '#fff',
          fontSize: 15,
        },
      },
      legend: {
        show: true,
        right: '3%',
        top: 20,
        itemWidth: 16,
        itemHeight: 10,
        itemGap: 15,
        icon: 'rect',
        textStyle: {
          padding: 4,
          color: '#fff',
          fontSize: 14,
          fontFamily: 'PingFangSC-Regular, PingFang SC',
          fontWeight: 400,
        },
      },
      grid: {
        left: '3%',
        right: '3%',
        bottom: '2%',
        containLabel: true,
      },
      xAxis: [
        {
          type: 'category',
          data: xAxisData,
          axisLabel: {
            color: '#fff',
            fontWeight: 300,
            padding: 0,
            formatter: xAxisLineFeed,
          },
          axisTick: {
            show: false,
          },
        },
      ],
      yAxis: [
        {
          type: 'value',
          axisLabel: { show: true, color: '#fff' },

          splitLine: {
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.4)',
            },
          },
        },
      ],
      series: [
        {
          name: '正常',
          type: 'bar',
          barWidth: 14,
          stack: 'Ad',
          color: 'rgba(37, 92, 209, 1)',
          data: normalData,
        },
        {
          name: '超龄',
          type: 'bar',
          stack: 'Ad',
          barWidth: 14,
          color: 'rgba(89, 241, 255, 1)',
          data: overageData,
        },
        {
          name: '无证',
          type: 'bar',
          barWidth: 14,
          color: 'rgba(53, 162, 255, 1)',
          stack: 'Ad',
          data: withoutLicenseData,
        },
      ],
    };

    return (
      <div
        className={classNames(styles.container)}
        onMouseOver={() => {
          clearTimeout(this.timer);
        }}
        onMouseOut={() => {
          clearTimeout(this.timer);
          this.autoTooltip();
        }}
      >
        <ReactEcharts
          ref={e => {
            this.echartsReact = e;
          }}
          option={option}
          notMerge={true}
          lazyUpdate={true}
          style={{
            width: '100%',
            height: '100%',
            backgroundColor: 'transparent',
            margin: '0',
            zIndex: 6,
          }}
        />
      </div>
    );
  }
}

export default PieChart;
