import React, { Component, ComponentClass } from 'react';
import styles from './index.less';
import map from '@/assets/images/schoolyardDeck/map.png';
import { UmiComponentProps } from '@/common/type';
import mainBg from '@/assets/images/campusSafety/bg.png';
import titleBg from '@/assets/images/campusSafety/header-title-bg-icon.png';

import DigitalFlipper from '../DigitalFlipper';

import { Data } from '@/models/flightDeck';

import SecurityPerson from '../SecurityPerson';
import Map from '../Map';
import axios from 'axios';
import SecurityInspection from '../SecurityInspection';
import Campus from '../Campus';
import ReactSeamlessScroll from 'react-seamless-scroll';
import baseData from '@/assets/campusSafety.json';
import Device from '../Device';
import DigitalFlipperNumber from '../DigitalFlipperNumber';

interface OwnProps {
  // isFull: boolean;
}
interface Props extends OwnProps, UmiComponentProps {
  register: Data;
  userType: Data;
  alarm: Data;
  specialTarget: Data;
  defRatio: Data;
  tecRatio: Data;
  safeSynthesize: Data;
  deviceCollect: Data;
  device: Data;
}
interface State {
  data: any;
}

class Deck extends Component<Props, State> {
  state = {
    data: null,
  };
  intervalTimer: any;
  async componentDidMount() {
    const { data } = await axios.get(
      '/paxyjsc/static/campusSafety.json?random=' + new Date().getTime(),
    );
    this.setState({ data });
  }
  componentWillUnmount() {
    clearInterval(this.intervalTimer);
  }
  // 安保人员
  renderSecurityPersonnelProfile = () => {
    const data = this.getData('securityPersonnelSituation') || {};

    return (
      <div className={styles.securityPerson}>
        {this.renderHeader('安保人员概览')}
        <SecurityPerson data={data} />
      </div>
    );
  };
  // 安全检查概览
  renderSecurityInspection = () => {
    const data = this.getData('securityInspection') || {};

    return (
      <div className={styles.securityInspection}>
        {this.renderHeader('安全检查概览')}
        <SecurityInspection data={data} />
      </div>
    );
  };
  getData = field => {
    const { data } = this.state;
    console.log(data);
    return data ? data[field] : null;
  };
  // 护学概况
  renderProtect = () => {
    const data = this.getData('protect') || [];

    const element = (item, index) => {
      const gradientColors = [
        'linear-gradient(0deg, #020D20, #164970)',
        ' linear-gradient(0deg, #020D20, #245A4C)',
        'linear-gradient(0deg, #020D20, #2E3069)',
        'linear-gradient(0deg, #020D20, #533C31)',
      ];
      const colors = [
        'rgba(70, 160, 255, 1)',
        'rgba(69, 255, 130, 1)',
        'rgba(154, 157, 255, 1)',
        'rgba(255, 167, 95, 1)',
      ];
      const gradientColor = gradientColors[index];
      const color = colors[index];

      return (
        <div key={item.key} className={styles.item} style={{ background: gradientColor }}>
          <div className={styles.value}>
            <span className={styles.number} style={{ color }}>
              {item.number}
            </span>
            <span>人</span>
          </div>
          <div className={styles.name}>{item.name}</div>
        </div>
      );
    };
    return (
      <div className={styles.protect}>
        {this.renderHeader('护学概况')}
        <div className={styles.content}>
          {data.slice(0, 2).map((item, index) => {
            return element(item, index);
          })}
        </div>
        <div className={styles.content}>
          {data.slice(2, 4).map((item, index) => {
            return element(item, index + 2);
          })}
        </div>
      </div>
    );
  };
  renderTitle = () => {
    const title = this.getData('title') || '';
    return (
      <div className={styles.titleContent}>
        <div data-text={title} className={styles.title}>
          {title}
        </div>
      </div>
    );
  };
  renderBg = () => {
    return (
      <div>
        {/* <img className={styles.bgHeader} src={bgHeader} /> */}
        <img className={styles.bg} src={mainBg} />
      </div>
    );
  };
  renderFlipCard = () => {
    const data: any = this.getData('indicator') || '';

    return <>{<DigitalFlipper data={data.value} title={data.name} />}</>;
  };
  renderHeader = text => {
    return (
      <div className={styles.headerTitle}>
        <div>
          <img className={styles.bg} src={titleBg} />
          <div className={styles.lineWrap}>
            <div className={styles.leftRect} />
            <div className={styles.line} />
            <div className={styles.rightRect} />
          </div>
        </div>
        <span>{text}</span>
      </div>
    );
  };
  // 总数
  renderStatistics = () => {
    const data: any = this.getData('statistics') || [];

    function toThousands(num) {
      return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
    }
    return (
      <div className={styles.statisticsWrap}>
        <div />
        {data.map((item, index) => {
          return (
            <div className={styles.item} key={index.toString(36) + index}>
              <div className={styles.left}>
                {<img src={require(`@/assets/images/campusSafety/${item.url}`)} />}
              </div>
              <div className={styles.right}>
                <div className={styles.name}>{item.name}</div>
                <div className={styles.value}>{toThousands(item.value || 0)}</div>
              </div>
            </div>
          );
        })}
        <div className={styles.bottom}>
          <div className={styles.lineWrap}>
            <div className={styles.leftRect} />
            <div className={styles.line} />
            <div className={styles.rightRect} />
          </div>
        </div>
        <div className={styles.top}>
          <div className={styles.lineWrap}>
            <div className={styles.leftRect} />
            <div className={styles.line} />
            <div className={styles.rightRect} />
          </div>
        </div>
      </div>
    );
  };

  // 校园概览
  renderCampus = () => {
    const data = this.getData('campus') || {};

    return (
      <div className={styles.campus}>
        {this.renderHeader('校园概览')}
        <Campus data={data} />
      </div>
    );
  };
  renderMap = () => {
    const data = this.getData('map') || {};
    return (
      <div className={styles.map}>
        <div className={styles.mapBgWrap}>
          <img src={map} className={styles.mapBg} />
        </div>
        <Map data={data} />
      </div>
    );
  };
  // 校园动态
  renderCampusDynamic = () => {
    const data: any = this.getData('campusDynamic') || {};
    console.log(data);
    const list = data.data || [];

    return (
      <div className={styles.campusDynamic}>
        {this.renderHeader('校园动态')}
        <div className={styles.table}>
          <div className={styles.thead}>
            <div className={styles.name}>区县预警学校名称</div>
            <div className={styles.event}>校园预警具体事件</div>
          </div>
          <div className={styles.tbody}>
            <ReactSeamlessScroll style={{ height: '236px' }}>
              {list.map((item, index) => {
                return (
                  <div key={index.toString(36) + index} className={styles.tr}>
                    <div className={styles.name}>{item.name}</div>
                    <div className={styles.event} title={item.event}>
                      {item.event}
                    </div>
                  </div>
                );
              })}
            </ReactSeamlessScroll>
          </div>
        </div>
      </div>
    );
  };
  renderDeviceBar = () => {
    const data = this.getData('deviceBar') || {};

    return (
      <div className={styles.deviceBar}>
        <Device data={data} />
      </div>
    );
  };
  // 智安设备概况
  renderDevice = () => {
    const data: any = this.getData('device') || [];
    const list = data.total || [];

    return (
      <div className={styles.device}>
        {this.renderHeader('智安设备概览')}
        <div className={styles.deviceContent}>
          <div className={styles.left}>
            {list.map((item, index) => {
              return (
                <div className={styles.item} key={index.toString(36) + index}>
                  <div className={styles.itemLeft}>
                    {<img src={require(`@/assets/images/campusSafety/${item.url}`)} />}
                  </div>
                  <div className={styles.itemRight}>
                    <div className={styles.name}>{item.name}</div>
                    <div className={styles.value}>
                      {item.value} <span className={styles.unit}>{item.unit}</span>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
          <div className={styles.right}>{this.renderDeviceBar()}</div>
        </div>
      </div>
    );
  };
  renderCampusNumber = () => {
    const data: any = this.getData('campusNumber') || [];

    return (
      <div className={styles.campusNumber}>
        {<DigitalFlipperNumber data={data.value} title={data.name} />}
      </div>
      // <div className={styles.campusNumber}>
      //   <div className={styles.name}>{data.name}</div>
      //   <ProgressBar percent={data.value} />
      // </div>
    );
  };

  render() {
    const { data } = this.state;
    return (
      <div className={styles.container}>
        {data && (
          <div className={styles.itemContent}>
            {this.renderTitle()}
            {this.renderFlipCard()}
            {this.renderCampus()}
            {this.renderSecurityPersonnelProfile()}
            {this.renderSecurityInspection()}
            {this.renderCampusDynamic()}
            {this.renderMap()}
            {this.renderStatistics()}
            {this.renderCampusNumber()}
            {this.renderDevice()}
            {this.renderBg()}
            <div className={styles.copyRight}>浙江立地信息科技有限公司技术支持</div>
          </div>
        )}
      </div>
    );
  }
}

export default Deck as ComponentClass<OwnProps, State>;
