import React from 'react';
import ReactEcharts from 'echarts-for-react';
import classNames from 'classnames';
import styles from './index.less';

type Props = {
  data: any;
};
type State = {
  data: any[];
};
let myChart: any = {};
let index = 0;
class PieChart extends React.Component<Props, State> {
  loopTooltipTimer: any;
  echartsReact: any;
  timer: any;
  constructor(props) {
    super(props);
    this.echartsReact = React.createRef();
  }
  state = {
    data: [],
  };
  componentDidMount() {
    const data = this.props.data.data;
    this.setState({ data });
    myChart = this.echartsReact.getEchartsInstance();
    this.autoTooltip();
  }
  componentWillUnmount() {
    clearTimeout(this.timer);
    myChart && myChart.clear();
  }

  // tooltip轮播
  autoTooltip = () => {
    const dataLength = this.state.data.length;
    const _this = this;
    this.timer && clearTimeout(this.timer);
    this.timer = setTimeout(function() {
      myChart.dispatchAction({
        type: 'showTip',
        seriesIndex: 0,
        dataIndex: index,
      });
      index++;
      if (index >= dataLength) {
        index = 0;
      }
      _this.autoTooltip();
    }, 2000);
    myChart.on('mouseout', params => {
      index = params.dataIndex;
    });
  };
  render() {
    const { data }: any = this.state;
    const yAxisData = (data || []).map(item => item.area) || [];
    const nurserySchoolData = (data || []).map(item => item.nurserySchool) || [];
    const middleAndPrimarySchoolData = (data || []).map(item => item.middleAndPrimarySchool) || [];
    const highSchoolData = (data || []).map(item => item.highSchool) || [];

    const option = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'none',
        },
        backgroundColor: 'rgba(15, 103, 171, 0.45)',
        borderWidth: 0,
        className: styles.tooltip,
        textStyle: {
          color: '#fff',
          fontSize: 15,
        },
      },
      legend: {
        show: true,
        top: 13,
        itemWidth: 16,
        itemHeight: 10,
        itemGap: 15,
        icon: 'rect',
        textStyle: {
          padding: 4,
          color: '#fff',
          fontSize: 14,
          fontFamily: 'PingFangSC-Regular, PingFang SC',
          fontWeight: 400,
        },
      },
      grid: {
        left: '6%',
        right: '3%',
        bottom: '10%',
        containLabel: true,
      },
      yAxis: [
        {
          type: 'category',
          data: yAxisData,
          inverse: true,
          axisLabel: {
            color: '#fff',
            fontWeight: 300,
            padding: 0,
          },
          axisTick: {
            show: false,
          },
        },
      ],
      xAxis: [
        {
          type: 'value',
          axisLabel: { show: false },
          splitLine: {
            show: false,
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.4)',
            },
          },
        },
      ],
      series: [
        {
          name: '幼儿园',
          type: 'bar',
          stack: 'Ad',
          barWidth: 24,
          color: 'rgba(4, 209, 255, 1)',
          data: nurserySchoolData,
        },
        {
          name: '中小学',
          type: 'bar',
          barWidth: 24,
          stack: 'Ad',
          color: 'rgba(85, 139, 254, 1)',
          data: middleAndPrimarySchoolData,
        },
        {
          name: '高校',
          type: 'bar',
          barWidth: 24,
          color: 'rgba(37, 92, 209, 1)',
          stack: 'Ad',
          data: highSchoolData,
        },
      ],
    };

    return (
      <div
        className={classNames(styles.container)}
        onMouseOver={() => {
          clearTimeout(this.timer);
        }}
        onMouseOut={() => {
          clearTimeout(this.timer);
          this.autoTooltip();
        }}
      >
        <ReactEcharts
          ref={e => {
            this.echartsReact = e;
          }}
          option={option}
          notMerge={true}
          lazyUpdate={true}
          style={{
            width: '100%',
            height: '100%',
            backgroundColor: 'transparent',
            margin: '0',
            zIndex: 6,
          }}
        />
      </div>
    );
  }
}

export default PieChart;
