import React, { useEffect, useRef } from 'react';
import Flipper from './Flipper';

interface Props {
  gotoNum: number;
  delay?: number;
}
const FlipClock = ({ gotoNum, delay = 0 }: Props) => {
  let flipperRef;
  let id;
  const oldRef = useRef(0);
  const createTimer = async gotoNum => {
    const diff = oldRef.current - gotoNum;
    let i = 0;

    id = setInterval(() => {
      if (i >= Math.abs(diff)) {
        clearInterval(id);
        oldRef.current = oldRef.current + i;
        i = 0;
        return;
      }
      if (diff < 0) {
        const current = oldRef.current + i;
        flipperRef.flipDown(current, current + 1);
      } else {
        const current = oldRef.current + i;
        flipperRef.flipUp(current, current - 1);
      }
      i++;
    }, 1000);
  };
  useEffect(() => {
    flipperRef.setFront(0);
    return () => clearInterval(id);
  }, []);
  useEffect(
    () => {
      oldRef.current !== gotoNum &&
        setTimeout(() => {
          createTimer(gotoNum);
        }, delay);
    },
    [gotoNum],
  );

  return (
    <Flipper
      ref={c => {
        if (c) {
          flipperRef = c;
        }
      }}
    />
  );

  // componentDidMount() {
  //   this.flipObjs = [
  //     this.refs.flipperHour1,
  //     this.refs.flipperHour2,
  //     this.refs.flipperMinute1,
  //     this.refs.flipperMinute2,
  //     this.refs.flipperSecond1,
  //     this.refs.flipperSecond2,
  //   ];
  //   this.init();
  //   // this.run();
  //   this.flipObjs[2].flipDown(0, 1);
  // }

  // // 开始计时
  // run() {
  //   this.timer = setInterval(() => {
  //     // 获取当前时间
  //     let now = new Date();
  //     let nowTimeStr = this.formatDate(new Date(now.getTime() - 1000), 'hhiiss');
  //     let nextTimeStr = this.formatDate(now, 'hhiiss');
  //     for (let i = 0; i < this.flipObjs.length; i++) {
  //       if (nowTimeStr[i] === nextTimeStr[i]) {
  //         continue;
  //       }
  //       console.log(nowTimeStr, nextTimeStr);
  //       this.flipObjs[i].flipDown(nowTimeStr[i], nextTimeStr[i]);
  //     }
  //   }, 1000);
  // }
};
export default FlipClock;
