import classNames from 'classnames';
import React, { Component } from 'react';
import styles from './flipper.less';
interface State {
  isFlipping: boolean;
  flipType: 'down' | 'up';
  frontTextFromData: number;
  backTextFromData: number;
}
interface Props {
  duration?: number;
}
class Flipper extends Component<Props, State> {
  isFlipping: boolean;
  constructor(props) {
    super(props);
    this.state = {
      isFlipping: false,
      flipType: 'down',
      frontTextFromData: 0,
      backTextFromData: 1,
    };
  }

  render() {
    const { isFlipping, flipType, frontTextFromData, backTextFromData } = this.state;
    return (
      <div
        className={classNames(styles['M-Flipper'], styles[flipType], isFlipping && styles['go'])}
      >
        <div
          className={classNames(styles.digital, styles.front, this._textClass(frontTextFromData))}
        />
        <div
          className={classNames(styles.digital, styles.back, this._textClass(backTextFromData))}
        />
      </div>
    );
  }

  // componentDidMount() {
  //     this.props.onRef(this)
  // }
  test() {
    console.log('test');
  }
  _textClass(number) {
    return styles['number' + number];
  }
  _flip(type, front, back) {
    // 如果处于翻转中，则不执行
    if (this.isFlipping) {
      return false;
    }
    this.setState({
      frontTextFromData: front,
      backTextFromData: back,
      // 根据传递过来的type设置翻转方向
      flipType: type,
      // 设置翻转状态为true
      isFlipping: true,
    });
    const { duration = 500 } = this.props;
    setTimeout(() => {
      this.setState({
        frontTextFromData: back,
        isFlipping: false,
      });
    }, duration);
  }
  // 下翻牌
  flipDown(front, back) {
    this._flip('down', front, back);
  }
  // 上翻牌
  flipUp(front, back) {
    this._flip('up', front, back);
  }
  // 设置前牌文字
  setFront(text) {
    this.setState({
      frontTextFromData: text,
    });
  }
  // 设置后牌文字
  setBack(text) {
    this.setState({
      backTextFromData: text,
    });
  }
}

export default Flipper;
