import React from 'react';
import ReactEcharts from 'echarts-for-react';
import classNames from 'classnames';
import styles from './index.less';
import * as echarts from 'echarts';

type Props = {
  data: any;
};
type State = {
  data: any[];
};
let myChart: any = {};
let index = 0;
class PieChart extends React.Component<Props, State> {
  loopTooltipTimer: any;
  echartsReact: any;
  timer: any;
  constructor(props) {
    super(props);
    this.echartsReact = React.createRef();
  }
  state = {
    data: [],
  };
  componentDidMount() {
    if (this.props.data.data) {
      const data = this.props.data.data;
      this.setState({ data });
      myChart = this.echartsReact.getEchartsInstance();
      this.autoTooltip();
    }
  }
  componentWillUnmount() {
    this.timer && clearTimeout(this.timer);
    myChart && myChart.clear();
  }

  // tooltip轮播
  autoTooltip = () => {
    const dataLength = this.state.data.length;
    const _this = this;
    this.timer && clearTimeout(this.timer);
    this.timer = setTimeout(function() {
      myChart.dispatchAction({
        type: 'showTip',
        seriesIndex: 0,
        dataIndex: index,
      });
      index++;
      if (index >= dataLength) {
        index = 0;
      }
      _this.autoTooltip();
    }, 2000);
    myChart.on('mouseout', params => {
      index = params.dataIndex;
    });
  };
  render() {
    const { data }: any = this.state;
    const xAxisData = (data || []).map(item => item.area) || [];
    const totalData = (data || []).map(item => item.total) || [];

    const option = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'none',
        },
        backgroundColor: 'rgba(15, 103, 171, 0.45)',
        borderWidth: 0,
        className: styles.tooltip,
        textStyle: {
          color: '#fff',
          fontSize: 15,
        },
      },
      legend: {
        show: false,
      },
      grid: {
        left: '10%',
        right: '3%',
        top: '14%',
        bottom: '9%',
        containLabel: true,
      },
      xAxis: [
        {
          type: 'category',
          data: xAxisData,
          axisLabel: {
            color: '#fff',
            fontWeight: 300,
            padding: 0,
          },
          axisTick: {
            show: false,
          },
        },
      ],
      yAxis: [
        {
          type: 'value',
          axisLabel: { show: true, color: '#fff' },
          axisLine: {
            show: true,
          },
          splitLine: {
            show: false,
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.4)',
            },
          },
        },
      ],
      series: [
        {
          barWidth: '100%',
          type: 'pictorialBar',
          barCategoryGap: '0%',
          // symbol: 'path://M0,10 L10,10 L5,0 L0,10 z',
          symbol: 'path://M0,10 L10,10 C5.5,10 5.5,5 5,0 C4.5,5 4.5,10 0,10 z',
          // symbol: 'path://path://M10 600 Q 95 0 180 600',
          color: 'rgba(4, 209, 255, 1)',
          itemStyle: {
            color: new echarts.graphic.LinearGradient(1, 0, 0, 1, [
              {
                offset: 0,
                color: '#4BECFB',
              },
              {
                offset: 0.6,
                color: '#3688D4',
              },
              {
                offset: 1,
                color: '#3688D4',
              },
            ]),
          },
          data: totalData,
        },
      ],
    };

    return (
      <div
        className={classNames(styles.container)}
        onMouseOver={() => {
          clearTimeout(this.timer);
        }}
        onMouseOut={() => {
          clearTimeout(this.timer);
          this.autoTooltip();
        }}
      >
        <ReactEcharts
          ref={e => {
            this.echartsReact = e;
          }}
          option={option}
          notMerge={true}
          lazyUpdate={true}
          style={{
            width: '100%',
            height: '100%',
            backgroundColor: 'transparent',
            margin: '0',
            zIndex: 6,
          }}
        />
      </div>
    );
  }
}

export default PieChart;
